.formLogin {
  /* position: absolute; */
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  padding: 40px 18px 53px 18px;
  width: 300px;
  background-color: #fff;
  box-shadow: 5px 10px 20px rgba(170, 178, 197, 0.4);
  border-radius: 30px;
}

.promtText {
  font-size: 10px;
  line-height: 12px;
  text-align: center;
  letter-spacing: 0.04em;
  color: #52555f;
  margin-bottom: 24px;
}

.formLabel {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}

.labelText {
  margin-bottom: 12px;
  width: 100%;
  font-style: normal;
  font-weight: 600;
  font-size: 10px;
  line-height: 12px;
  color: black;
}

.labelTextSecond {
  text-align: left;
}

.formInput {
  outline: none;
  border: none;
  border-radius: 30px;
  background-color: #f6f7fb;
  padding: 17px 20px;
}
input[type='email']::placeholder {
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.04em;
  color: #a6abb9;
}
input[type='password']::placeholder {
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.04em;
  color: #a6abb9;
}
.containerButton {
  display: flex;
  justify-content: space-between;
}

.button {
  width: 125px;
  height: 44px;
  background-color: #f5f6fb;
  border-radius: 16px;
  border: none;
  padding: 0;
  color: #52555f;
  font-size: 12px;
  font-weight: bold;
  box-shadow: #52555f;
  filter: drop-shadow(1px 3px 5px rgba(82, 85, 95, 0.15));
  cursor: pointer;
}

.button:hover {
  background-color: #ff751d;
  color: #fff;
  filter: drop-shadow(1px 3px 5px rgba(255, 107, 8, 0.35));
  transition: 0.5s linear;
}

.animationGoogle {
  text-align: center;
}

@media screen and (min-width: 768px) {
  .formLogin {
    z-index: 4;
    width: 436px;
    /* top: -64%; */
    padding: 50px 85px 53px 85px;
  }

  .fontText {
    margin: 5px 0 0 225px;
    font-size: 16px;
    line-height: 19px;
  }

  .promtText {
    font-size: 12px;
    line-height: 14px;
    margin-bottom: 20px;
    text-align: left;
  }

  .labelText {
    font-size: 12px;
    line-height: 14px;
  }
}

@media screen and (min-width: 1280px) {
  .formLogin {
    margin: 0;
    width: 436px;
    /* top: -409px;
    left: 75%; */
  }
}
